body {
  margin: 0;
  padding-top: 0;
  padding-bottom: 12px;
}

.search-form {
  max-width: 100%;
  height: 40px;
  background-color: #6495ed;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.input-place {
  text-indent: 5px;
  border: none;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.btn-search {
  border: none;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.icon-search {
  max-width: 15px;
  max-height: 12px;
  margin: 0 auto;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
  padding: 12px;
  display: grid;
}

.load-more {
  width: 150px;
  height: 32px;
  color: #fff;
  background-color: #191970;
  border-radius: 10px;
  margin: 0 auto;
  font-size: larger;
  display: block;
}

.photo-card {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.image {
  width: 100%;
  height: 80%;
}

.info {
  width: 100%;
  height: 20%;
  flex-flow: wrap;
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  display: inline-flex;
}

